@import 'font';
$font-family-base: "Open Sans", sans-serif;

// Required
@import "../bootstrap/functions";
@import "../bootstrap/variables";
@import "../bootstrap/mixins";

@import "../bootstrap/root";
@import "../bootstrap/reboot";
@import "../bootstrap/type";
@import "../bootstrap/images";
@import "../bootstrap/grid";

html,body,div
{
  height: 100%;
}

h1,h2,h3,h4,h5,h6
{
  font-weight: 700;
}

.background-image
{
  background-image: url('/images/apples-background-1-xs.jpg');
  background-repeat: no-repeat;
  background-size: contain;
  background-position-x: center;
}

@include media-breakpoint-up(sm) {
  .background-image {
    background-image: url('/images/apples-background-1-sm.jpg');
  }
}

@include media-breakpoint-up(md) {
  .background-image {
    background-image: url('/images/apples-background-1-md.jpg');
  }
}

@include media-breakpoint-up(lg) {
  .background-image {
    background-image: url('/images/apples-background-1.jpg');
  }
}

.intro-features-wrap img
{
  height: 150px;
}
